// page-home
.conference-section-1 {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    .overlay-red {
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: var(--tc-theme-primary);
        mix-blend-mode: multiply;
        z-index: 1;
        &::after {
            position: absolute;
            content: '';
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 709px;
            height: 709px;
            border-radius: 709px;
            background: linear-gradient(149deg, #292929 13.64%, rgba(0, 0, 0, 0) 84.89%);
            mix-blend-mode: luminosity;
            z-index: -2;
            @media (max-width: 767px) {
                width: 500px;
                height: 500px;
            }
        }
    }
    svg {
        margin-top: -10px;

        text {
            font-weight: 600;
            text-transform: uppercase;
            letter-spacing: -0.8px;
            fill: transparent;
            stroke: #fff;
            stroke-width: 2px;
            animation: animated-stroke 2s;
            text-anchor: middle;
            dominant-baseline: middle;
        }
    }
    h1 {
        color: var(--White, #fff);
        font-family: 'Libre Franklin';
        font-style: normal;
        font-weight: 600;
        line-height: 74px; /* 92.5% */
        letter-spacing: -0.8px;
        margin-top: -40px;
    }
}
@keyframes animated-stroke {
    0% {
        stroke-dasharray: 0 80%;
    }
    100% {
        stroke-dasharray: 10% 0%;
        stroke-dashoffset: 25%;
    }
}
.mix-blend-mode {
    mix-blend-mode: luminosity;
}
.conference-section-2 {
    .users-sponsors {
        position: relative;
        border-radius: 20px;
        border-top: 1px solid var(--tc-theme-primary);
        border-bottom: 1px solid var(--tc-theme-primary);
        background: #fff1f3;
        padding-top: 50px;
        padding-bottom: 50px;
        .svg {
            left: 180px;
            z-index: 1;
        }
    }
}
.conference-services-section-2 {
    .image-bg {
        min-height: 640px;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }
}
.conference-section-3,
.conference-services-section-3,
.conference-speakers-section-2 {
    .conference-title {
        color: var(--Black, #292929);
        text-align: center;
        font-size: 300px;
        font-style: normal;
        font-weight: 600;
        line-height: 100%; /* 300px */
        letter-spacing: 60px;
        text-transform: uppercase;
        opacity: 0.025;
        margin-bottom: 0;
    }
    .icon-shape {
        color: #fff;
        text-align: center;
        font-family: 'Libre Franklin';
        font-size: 40px;
        font-style: normal;
        font-weight: 600;
        line-height: 100%; /* 40px */
        letter-spacing: 8px;
        text-transform: uppercase;
    }
    // nav-tabs
    .nav-tabs {
        --bs-nav-tabs-border-width: var(--bs-border-width);
        --bs-nav-tabs-border-color: var(--bs-border-color);
        --bs-nav-tabs-border-radius: 0;
        --bs-nav-tabs-link-active-color: var(--tc-theme-primary);
        --bs-nav-tabs-link-hover-border-color: transparent;
    }
    .nav {
        --bs-nav-link-padding-x: 20px;
        --bs-nav-link-padding-y: 10px;
        --bs-nav-link-font-weight: var(--tc-fw-bold);
        --bs-nav-link-color: var(--tc-system-black);
        --bs-nav-link-hover-color: var(--tc-theme-primary);
    }
    .nav-link {
        display: block;
        padding: var(--bs-nav-link-padding-y) var(--bs-nav-link-padding-x);
        font-size: 14px;
        font-weight: 600;
        color: var(--bs-nav-link-color);
        .time {
            color: var(--tc-body-text);
            font-size: 14px;
            font-family: 'Libre Franklin';
            font-style: normal;
            font-weight: 400;
            line-height: 100%;
            text-transform: uppercase;
            transition: 0.3s ease-in-out;
            margin-top: 8px;
        }
        .underline {
            width: 140px;
            height: 2px;
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            margin: auto;
            opacity: 0;
            background: var(--tc-system-black);
            transition: 0.3s ease-in-out;
        }
    }
    .nav-tabs .nav-item.show .nav-link,
    .nav-tabs .nav-link.active {
        color: var(--bs-nav-tabs-link-active-color);
        background-color: transparent;
        border-color: transparent;
        .time {
            color: var(--tc-system-black);
        }
        .underline {
            opacity: 1;
            transition: 0.3s ease-in-out;
        }
    }
}
.portfolio-project {
    &:hover {
        .line-bottom {
            --bs-border-opacity: 1;
            border-color: var(--tc-theme-primary) !important;
            transition: all 200ms ease-in-out 50ms;
        }
    }
    .project {
        strong {
            font-size: 64px;
            font-style: normal;
            font-weight: 600;
            font-family: 'Libre Franklin';
            line-height: 74px; /* 115.625% */
            letter-spacing: -0.64px;
            text-align: center;
            -webkit-text-stroke-width: 2;
            -webkit-text-stroke-color: var(--tc-system-red);
            background: var(--tc-system-red);
            -webkit-background-clip: text;
            -webkit-text-stroke: 2px var(--tc-system-red);
            color: var(--tc-system-white);
            font-optical-sizing: auto;
            paint-order: stroke fill;
        }
    }
}
.conference-section-4 {
    // nav-tabs
    .nav-link {
        display: block;
        font-size: 14px;
        font-weight: 600;
        text-transform: uppercase;
        color: rgba(255, 255, 255, 0.4);
        background-color: transparent;
        border: none !important;
        border-radius: var(--bs-nav-tabs-border-radius);
    }
    .nav-tabs .nav-item.show .nav-link,
    .nav-tabs .nav-link.active {
        color: var(--tc-system-white);
        background-color: transparent;
    }
    .shape-img {
        left: -184px;
        top: -71px;
    }
}
.card-team-conference {
    .team-overlay {
        content: '';
        position: absolute;
        transition: all 0.3s linear;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: var(--tc-theme-primary);
        opacity: 0;
        transform: translateX(-50%);
        .icon {
            position: relative;
            width: 34px;
            height: 34px;
            border-radius: 34px;
            svg {
                height: 14px;
                width: 14px;
                fill: var(--tc-system-white);
                opacity: 0.5;
            }
            &:hover {
                background: #292929;
                svg {
                    fill: var(--tc-system-white);
                    opacity: 1;
                    transition-duration: 0.3s;
                }
            }
        }
    }
    &:hover {
        .team-overlay {
            opacity: 1;
            transform: translateX(0%);
        }
    }
}
.conference-home-section-5,
.conference-ticket-section-1 {
    .tag-recommended {
        display: flex;
        width: 136px;
        height: 28px;
        top: 0;
        left: 164px;
        justify-content: center;
        align-items: center;
        gap: 10px;
        border-radius: 0px 0px 10px 10px;
        background: var(--tc-theme-primary);
    }
}
.conference-home-section-6 {
    .bg-img {
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        position: relative;
        .overlay-red {
            position: absolute;
            content: '';
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background: #841213;
            mix-blend-mode: multiply;
            z-index: 1;
        }
        .time {
            fill: rgba(255, 255, 255, 0.01);
            border: 1px solid rgba(255, 255, 255, 0.2);
            backdrop-filter: blur(20px);
        }
    }
}
// page-services
.conference-services-section-header {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    .overlay-red {
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: var(--tc-theme-primary);
        mix-blend-mode: multiply;
        z-index: 1;
    }
}
.conference-event-details-section-header {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    .overlay-red {
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: var(--tc-theme-primary);
        mix-blend-mode: multiply;
        z-index: 1;
    }
}
.conference-services-section-1 {
    .card {
        transition: all 0.3s linear;
        &:hover {
            border-color: var(--tc-system-black) !important;
            .icon-shape {
                background: var(--tc-theme-primary);
                border-color: var(--tc-theme-primary) !important;
                transition: all 0.3s ease;
                svg {
                    path {
                        fill: white;
                        transition: all 0.3s ease;
                    }
                }
            }
        }
    }
}
// event-details
.conference-event-details-section-1 {
    .m-navigative {
        margin-top: -150px;
    }
}
// speakers-details

.conference-speaker-details-section-1 {
    .progress,
    .progress-stacked {
        --bs-progress-height: 4px;
        --bs-progress-font-size: 0.75rem;
        --bs-progress-bg: #e9e9e9;
        --bs-progress-border-radius: 20px;
        --bs-progress-box-shadow: var(--bs-box-shadow-inset);
        --bs-progress-bar-color: #fff;
        --bs-progress-bar-bg: var(--tc-theme-primary);
        --bs-progress-bar-transition: width 0.6s ease;
        display: flex;
        height: var(--bs-progress-height);
        overflow: hidden;
        font-size: var(--bs-progress-font-size);
        background-color: var(--bs-progress-bg);
        border-radius: var(--bs-progress-border-radius);
    }
}
.card {
    transition: all 0.3s ease-in-out;
    .card-header {
        a {
            .arrow {
                &::before {
                    transition: 0.3s;
                    transform: rotate(0deg);
                    font-family: bootstrap-icons;
                    content: '\F2EA';
                    color: var(--tc-system-navy-blue-3);
                    font-size: 20px;
                }
            }
            border-bottom: 1px solid transparent;
            &.collapsed {
                border-bottom: 1px solid rgba(255, 255, 255, 0.2);
                .arrow {
                    background-color: transparent;
                    &::before {
                        transition: 0.3s;
                        color: var(--tc-system-navy-blue-3);
                        transform: rotate(180deg);
                        content: '\F4FE';
                    }
                }
            }
        }
    }
}
.btn-filter {
    border: none;
    border-radius: 0;
    text-transform: uppercase;
    color: rgba(41, 41, 41, 0.4);
    &.active {
        color: rgba(41, 41, 41, 1);
        text-decoration: underline;
    }
}
.team-filter {
    .masonary-active {
        .card-news {
            margin: 15px 0;
        }
    }
    .grid-sizer,
    .grid-item {
        width: 50%;
    }
    @media (min-width: 1400px) {
        .grid-sizer,
        .grid-item {
            width: 33.33%;
        }
    }
    /* 2 columns */
    .grid-item--width2 {
        width: 66.66%;
    }
}
